.btn-mz {
    padding: 5px 20px;
    font-size: 16px;
    cursor: pointer;
    transition: all .3s;
    display: inline-block;
    font-family: Comfortaa, cursive;
    font-weight: 700;
    text-align: center;
    border: 2px solid transparent;
    min-width: 120px
}

.btn-mz:first-letter {
    text-transform: uppercase
}

.btn-mz.btn--full {
    width: 100%;
    text-align: center
}

.btn-mz.is--disabled {
    cursor: not-allowed;
    pointer-events: none
}

.btn-mz.is--disabled span {
    opacity: .52
}

.btn-mz:focus {
    outline: none
}

.btn-mz:hover {
    color: #fff;
    text-decoration: none
}

.btn--group .btn-mz--third,
.btn--group .btn-mz:nth-of-type(2) {
    margin-left: 20px
}

.btn--brand-color {
    --color: #000077
}

.btn--red-color {
    --color: #F86156;
}

.btn--info-color {
    --color: #62b4f9
}

.btn--yellow-color {
    --color: #FFCA65;
}

.btn--product-color {
    --color: #f80
}

.btn--details-color {
    --color: #ff5252
}

.btn--business-color {
    --color: #b604b6
}

.btn--userinput-color {
    --color: #63b4f9
}

.btn--radius {
    border-radius: 31px
}

.btn--outline {
    color: var(--color);
    border-color: var(--color);
    background-color: transparent
}

.btn--filled {
    background-color: var(--color);
    color: #fff
}

.btn-filled {
    background-color: white;
    color: var(--color);
}

.btn--shadow {
    box-shadow: 0 0 6px var(--color);
    transition: all .5s
}

.btn--shadow:hover {
    box-shadow: 0 0 27px 0 var(--color)
}

.btn-shadow {
    box-shadow: 0 0 6px white;
    transition: all .5s
}

.btn-shadow:hover {
    box-shadow: 0 0 27px 0 white;
}

@media screen and (min-width:320px) and (max-width:1023px) {
    .btn--shadow {
        box-shadow: 0 0 27px 0 var(--color)
    }
    .btn--group .btn-mz--third {
        margin-left: 0
    }
}

